.reg{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    &__form{
        display: flex;
        flex-direction: column;
        max-width: 220px;
        gap: 20px;
        margin: 20vh  auto;
        h5{
            color: $color_blue_btn;
            font-size: 30px;
            font-weight: 600;
        }
        &-input{
            padding: 10px 0;
            width: 100%;
        }
        &-btns{
            display: flex;
            gap: 10px;
            &-btn{
                background-color: $color_blue_btn;
                color: $color_gray_disabled;
                width: 100%;
                padding: 10px 0;
                border-radius: 8px;
                font-size: 16px;
                font-weight: 600;
                &-active{
                    background-color: $color_gray_disabled;
                    color: $color_blue_btn;
                }
            }
            &-link{
                margin: auto 0;
                span{
                    color: blue;
                }
            }
        }
    }
    &__users{
        &-item{
            display: flex;
            flex-direction: column;
            gap:15px;
            padding: 20px;
            padding: 10px;
            border: 1px solid $color_gray_disabled;
            margin: 20px;
            border-radius: 8px;
            &-text{
                display: flex;
                gap:5px;
            }
            &-coords{
                display: flex;
                gap:15px;
                &-label{
                    font-style: italic;
                }
            }
        }
    }
}